.form-section .back-btn {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 1rem;
}
.form-section .back-btn > .text {
  display: none;
}

.back-btn {
  background: transparent;
}
.back-btn > .image {
  background: #fbf5f9;
  border: 0.01rem solid #cdcdcd;
  border-radius: 0.3rem;
  padding: 0.5rem 0.7rem;
}
.back-btn > .text {
  font-weight: bold;
}